<template>
<div>
<app-header></app-header>
  <div class="center" style="margin-top: 200px;">
      <div class="col-md-4">
        
        
        <div class="col-12">
            <router-link to ="/home">
            <button style="margin-top: 10px;" class="btn btn-primary btn-block reset-border">Audience poll</button>
            </router-link>
        </div>
        <div class="col-12">
          <router-link to ="/survey"> 
          <button v-bind:title="title" style="margin-top: 10px;" class="btn btn-primary btn-block reset-border" >Survey Poll</button>
          </router-link>
        </div>
        
              
      </div>
  </div>
</div>
</template>

<script>
import axios from 'axios';
import Loader from './Loader';
import Header from './Header';
export default {
    components:{
        Loader,
            appHeader:Header
    },
    data(){
        return{
            name: '',
            code: '',
            isIncorrectCode: false,
            showLoader: false,
            quiz: '',
           
        }
    },
    methods:{
        validateCode: function(){
            
            socket.emit('addUser', this.name);
            this.showLoader = true; 
            axios.get('./static/json/questions.json')
            .then((response) => {
                //console.log(response.data);

                for(let data of response.data){
                    if(data.code == this.code){
                        this.quiz = data.quiz;

                        this.$router.push({name: 'Quiz', params:{quizData: this.quiz}})
                        break;
                    }
                      
                }
                

                if(this.quiz == ''){
                    this.isIncorrectCode = true;
                }

                this.showLoader = false;
                console.log(this.quiz);

            })
            .catch((error) => {
                console.log(error);
                this.showLoader = false;
            });

        }
    },
    computed:{
        'disable': function(){
            if(this.code.length < 4 || this.name.length < 4){
                return true;
            }

            return false;
        }
    }

}
</script>

<style scoped>

    .center{
        display: flex;
        justify-content: center;
    }

    .reset-border{
        border-radius: 0;
    }
    .btn:hover{
        transform: scale(1.1);
    }
</style>


