<template>
  <div>
        <app-Header></app-Header>
        
        
        <div class="content">
        <app-chart></app-chart>
        </div>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
       
        <app-footer></app-footer>
  </div>
</template>

<script>
import Header from './Header';
import Chart from './Chart.vue';
import Footer from './Footer.vue';

export default {
 components:{
  
        appHeader: Header,
        appChart: Chart,
        appFooter: Footer
    }
   
}
</script>

<style scoped>
body{
  
     background: -webkit-linear-gradient(top,#e3e3e3,white,#e3e3e3);
}
.content{
  margin-top:60px;
}
</style>



