import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Admin from '@/components/Admin'
import Quiz from '@/components/Quiz'
import Quiz1 from '@/components/Quiz1'
import Poll from '@/components/Poll'
import Aboutus from '@/components/Aboutus'
import Rule from '@/components/Rule'
import Contactus from '@/components/Contactus'
import Index from '@/components/Index'
import Spolling from '@/components/Spolling'
import Survey from '@/components/Survey'
import Type from '@/components/Type'
import Dir from '@/components/Dir'

Vue.use(Router)

export default new Router({
  routes: [
    
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    { 
      path: '/aboutus',
      name: 'Aboutus',
      component: Aboutus

    },
    {
      path: '/contactus',
      name: 'Contactus',
      component: Contactus
    },
    {
      path: '/rule',
      name: 'Rule',
      component: Rule

    },
    {
      path: '/admin',
      name: 'Admin',
      component: Admin
    },
    {
      path: '/quiz',
      name: 'Quiz',
      component: Quiz,
      props: true
    },
    {
      path: '/quiz1',
      name: 'Quiz1',
      component: Quiz1,
      props: true
    },
    {
      path: '/poll',
      name: 'Poll',
      component: Poll,
      props: true
    },
    {
      path: '/spolling',
      name: 'Spolling',
      component: Spolling
    },
    {
      path: '/survey',
      name: 'Survey',
      component: Survey
    },
    {
      path: '/type',
      name: 'Type',
      component: Type
    },
    {
      path: '/dir',
      name: 'Type',
      component: Dir
    }
  ]
})



