 <template>
    <div class="container">
    <div class= "col-10 col-sm-10 col-md-10 col-lg-8 col-xl-8">
    <nav class="navbar navbar-expand-md navbar-dark bg-dark fixed-top">
      <a class="navbar-brand" style="margin-left:15px;" href="#">Live Polling</a>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExampleDefault" aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarsExampleDefault">
        <ul class="navbar-nav ml-auto">
          
              <router-link to ="/" tag="li" active-class="active" exact><a class="nav-link"> <i class="fa fa-home" style="font-size:24px; margin-right:5px;"></i>Home</a></router-link>
              <router-link to ="/aboutus" tag="li" active-class="active"><a class="nav-link"><i class="fa fa-vcard" style="font-size:24px; margin-right:5px;"></i>About Us</a></router-link>
              <router-link to ="/contactus" tag="li" active-class="active"><a class="nav-link"><i class="fa fa-phone" style="font-size:24px; margin-right:5px;"></i>Contact Us</a></router-link>
                <router-link to ="/rule" tag="li" active-class="active" ><a class="nav-link"> <i class="fa fa-edit" style="font-size:24px; margin-right:5px;"></i>Rule</a></router-link>
                <router-link to ="/home" tag="li" active-class="active" ><a class="nav-link"><i class="fa fa-bar-chart" style="font-size:24px;"></i>Audience Poll</a></router-link>
                <router-link to ="/survey" tag="li" active-class="active" ><a class="nav-link"><i class="fa fa-bar-chart" style="font-size:24px;"></i>Survey Poll</a></router-link>
               <!--router-link to ="/spolling" v-bind:title="title" tag="li" active-class="active" ><a class="nav-link"><i class="fa fa-bar-chart" style="font-size:24px;"></i>Poll</a></router-link-->
              
        </ul>
      </div>
   
    </nav>
</div>
</div>
</template>

<script>
export default {
    data(){
        return{
        
        }
    },
  directives: {
            'local-highlight': {
                bind(el, binding, vnode) {
                    var delay = 0;
                    if (binding.modifiers['delayed']) {
                        delay = 3000;
                    }
                    if (binding.modifiers['blink']) {
                        let mainColor = binding.value.mainColor;
                        let secondColor = binding.value.secondColor;
                        let currentColor = mainColor;
                        setTimeout(() => {
                            setInterval(() => {
                                currentColor == secondColor ? currentColor = mainColor : currentColor = secondColor;
                                if (binding.arg == 'background') {
                                    el.style.backgroundColor = currentColor;
                                } else {
                                    el.style.color = currentColor;
                                }
                            }, binding.value.delay);
                        }, delay);
                    } else {
                        setTimeout(() => {
                            if (binding.arg == 'background') {
                                el.style.backgroundColor = binding.value.mainColor;
                            } else {
                                el.style.color = binding.value.mainColor;
                            }
                        }, delay);
                    }
                }
            }
        
        
}
}
</script>

<style scoped>
.navbar{
    width:97%;
    margin: 9px auto;
}
</style>




